.homescreen{
    max-width: 1300px;
    margin: 1rem auto;
}
.homescreen__title{
    font-size: 1.5rem;
    color: #171717;
    margin-bottom: 1rem;
    margin-left: 8px;
}
.homescreen__products{
    display: grid;
    grid-template-columns: repeat(4,1fr);
}
@media(max-width:900px){
    .homescreen__products{
        grid-template-columns: repeat(3,1fr);
    }
}
@media(max-width:600px){
    .homescreen__products{
        grid-template-columns: repeat(1,1fr);
    }
}
@media(max-width:400px){
    .homescreen__products{
        grid-template-columns: repeat(1,1fr);
    }
}