.cartscreen{
    display: flex;
    max-width: 1300px;
    margin: 2rem auto;
}
.cartscreen h2{
    margin-bottom: 1rem;
}
.cartscreen__left{
    flex: 0.7;
    margin: 1rem;
    background-color: transparent;
}
.cartscreen__right{
    flex: 0.3;    
    background: rgb(226, 225, 225);
    margin-right: 1rem;
    box-shadow: 0 1px 4px rgba(0,0,0,0.4);
    height: fit-content;
   
}
.cartscreen__right div{
    border-bottom: 1px solid #171717 ;
    padding: 1rem;

}
.cartscreen__right div:last-child{
    border: none;
}
.cartscreen__info p{
    padding: 8px;
}
.cartscreen__right div button{
    padding: 10px 17px;
    background: #171717;
    color: white;
    width: 100%;
    border: 1px solid #171717;
    cursor: pointer;

}
.cartscreen__right button:hover{

    opacity: 0.9;
    


}
 
@media(max-width:1320px)
{
    .cartscreen{
        max-width: 900px;
    }
}
@media(max-width:900px)
{
    .cartscreen{
        max-width: 800px;
        flex-direction: column;
    }
    .cartscreen__right{
        margin: 1rem;
    }
}
